.header{
 color: #ffffff;
 background: linear-gradient(90deg,#cb11ab,#481173);

 &-wrap{
    display: flex;
    flex-direction: row;
    gap: 20px;
    justify-content: space-between;
    align-items: center;
    padding: 50px 0px;
 }
}

.burger-menu{
    width: 48px;
    height: 48px;
    color: #ffffff;
    border: 1px solid rgba($color: #ffffff, $alpha: 0.5);
    border-radius: 5px;
    background-image: url('../images/menu-hamburger-white.svg');
    background-position: center; 
    background-repeat: no-repeat; 
    background-size: 80%;
    cursor: pointer;
}
.burger-menu:hover{
    border: 1px solid rgba($color: #ffffff, $alpha: 1.0);
}

.logo{
    text-transform: uppercase;
    font-size: 33px;
    font-weight: 400;
    cursor: pointer;
}

.input{
    flex: 1;
    height: 56px;
    border-radius: 28px;
    border: none;
    background-color:rgba($color: #f1a3e4, $alpha: 0.4) ;
    padding: 0px 25px;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.42;
    color: #ffffff;

    &::placeholder{
        color: #ffffff;
        opacity: 0.6;
    }
}

input:hover{
    background-color:rgba($color: #f1a3e4, $alpha: 0.75) ;
}

input:active{
    border: none;
}


.shoping-cart{
    cursor: pointer;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    &__icon{
        width: 32px;
        height: 32px;
        background-image: url(../images/shopping-cart_icon.svg);
        background-position: center; 
        background-repeat: no-repeat; 
        background-size: 100%;
        
    }

    &__p{
        margin: 0;
    }
}